:root{
    --margins:350px;
    --mainSize:20px;
}
html{scroll-behavior: smooth;}
.active-scroll-spy {
   
    color:white !important;
    
  }
 
*{box-sizing: border-box;
    font-family: Museo Sans Cyrl,sans-serif;
    margin:0;
    padding:0;
    }
    img{max-width: 100%;}
    section{min-height: 80vh;}
    a{text-decoration: none;}
    body{margin:0px;}
    
    #wrapper{position:sticky;top:70px;z-index:111;}
    #navbar{
        width:100%;
   
      background: rgb( 0 0 0);
    display: flex;
    justify-content: space-between;
    position: fixed;
    top:0px;
    z-index: 111;
    padding:20px var(--margins) 20px var(--margins);
    font-size: var(--mainSize);
    a{text-decoration: none;color:rgb(136 136 136);}
    a:hover{color:white;}
    }
    
    .nav-left{
        width:80%;
    display: flex;
    justify-content:flex-start;
    }
    .lang{display: flex;cursor: pointer;font-size: var(--mainSize);color:white;}
    .nav{margin-right:29px;}
    
    .line{max-width:100%;height:1px;background: black;margin:85px var(--margins) 0px var(--margins);visibility: hidden;}

    /////home/////
    #home{
       
        padding: 52px var(--margins) 65px var(--margins);
        width:100%;
    }
    .home-top{
        display: flex;
        justify-content: space-between;
        h1{font-size: 47px;}
        p{font-size: var(--mainSize);margin-top: 70px;}
        
    }
    .img-wrapper{
        background: url(img/background.jpg);
        margin: 30px 0px 65px 0px;
         width:100%;
         display:flex;justify-content: center;
         background-size: cover;
         border-radius: 10px;
         padding: 10px 0px 0px 0px;
        }
.main-img{max-height: 70vh;}
//// END OF HOME////
///////ABOUT./////
#about{
    background: rgb(246 246 246);
 padding:296px 641px 205px 641px;

 text-align: center; 
 font-size: var(--mainSize);
}
.about-me{
    font-size: 30px;
}
.p{margin-top: 30px;line-height: 24px;}
/////END OF ABOUT/////
/// SKILLS?????
#skills{
    background: white;
    padding:99px 184px 50px 184px;
    
}
.skills-head{text-align: center;
padding: 99px 0px 60px 0px;
font-size: 34px;
}
.skills-top{display:flex;justify-content: space-around;}
.skills-bottom{display: flex;justify-content: space-between;margin-top: 78px;}
.skills-img{height: 136px;}
.both{text-align: center;font-size: 18px;color:rgb(130 130 130);}
.desk{margin-top: 37px;}
//////end of skills////


//////PORTFOLIO/////////
#portfolio{
    
}
.site-img{
    width:100%;
    height:25vh;
    transition: 0.1s;
    background-size: cover;
}

.inner{color:white;width:100%;
height:inherit;
font-size: 75px;
font-weight: 400;
padding-left: 184px;

display: flex;
align-items: center;
text-decoration: underline;
opacity: 0.9;
transition: 0.5s;

}
.inner:hover{background: rgba(1,2,3,0) ;
   
  p{display: none;}
   }
   

    .designer{background-image: url(img/backgrounds/designer.jpg);}
    .inner-designer{background: rgb(115,90,53);}

    .work5{background-image: url(img/backgrounds/work5.jpg);}
    .inner-work5{background: rgb(66,91,62);}

    .pipernet{background-image: url(img/backgrounds/pipernet.jpg);}
    .inner-pipernet{background: rgb(64,117,98);}

    .nevatrip{background-image: url(img/backgrounds/nevatrip.jpg);}
    .inner-nevatrip{background: rgb(117,112,64);}

    .grocery{background-image: url(img/backgrounds/grocery.jpg);}
    .inner-grocery{background: rgb(115,64,64);}


    .bike{background-image: url(img/backgrounds/bike.jpg);}
    .inner-bike{background: rgb(64,115,118);}
    //////////////   end of portfolio //////
    /////////Contacts///////
    #contacts{text-align: center;padding-top: 128px;background: white;}
.head-contacts{
   font-size: 34px;
   margin-bottom: 25px;
}
.contacts-p{font-size: 18px;}
.mail{color:white;background: rgb(7 7 7);
    margin-top:56px;
    margin-bottom: 56px;
font-size: 18px;
border-radius: 25px;
padding:10px 20px 10px 20px;
min-width: 300px;
}
.icons{margin-bottom: 100px;display:flex;align-items: center;justify-content: center;}
.fa{font-size: 40px;margin:10px;color:black;}
.fa-github{font-size: 44px;}
.hh{height:40px;margin-top:  4px ; margin-left:10px;filter:grayscale(100%)}







    @media(max-width:1800px){
        :root{--margins:250px;}
        #about{padding:296px 400px 205px 400px;}
    }
    @media(max-width:1600px){
        
        #about{padding:296px 350px 205px 350px;}
        #skills{ padding:99px 84px 50px 84px;}
        .skills-img{height: 120px;}
    }
    @media(max-width:1300px){
        :root{
        --mainSize:18px;
        }
        #about{padding:106px 350px 105px 350px;}
        #navbar{ padding:15px var(--margins) 15px var(--margins);font-size: 15px;}
        .lang{font-size: 14px;}
        #wrapper{top:40px;}
        .line{margin:10px var(--margins) 10px var(--margins);background: lightgrey;}
        #skills{ padding:0px 84px 130px 84px;}
        .skills-img{height: 90px;}
        .home-top{
            h1{font-size: 33px;}
        p{font-size: var(--mainSize);}
        }
        .inner{font-size: 50px;padding-left: 130px;}
        .head-contacts{font-size: 30px;}
        .contacts-p,.mail{font-size: 17px;}
    }
    @media(max-width:1100px){
        :root{
          --margins:100px;
        --mainSize:17px;
        }
        .contacts-p, .mail{font-size: 15px;}
        #about{padding:100px 170px 100px 170px;}
        #skills{ padding:99px 70px 50px 70px;}
        .skills-img{height: 80px;}
        .home-top{h1{font-size: 35px;}}
        .inner{font-size: 40px;padding-left: 100px;}
        .head-contacts{font-size: 25px;}
        .contacts-p,.mail{font-size: 16px;}

    }
    @media(max-width:900px){
        :root{
          --margins:50px;
        --mainSize:14px;
        }
        #about{padding:100px 170px 100px 170px;}
        #skills{ padding:99px 70px 50px 70px;}
        .skills-img{height: 60px;}
        .both{font-size: 12px;}
        .home-top{h1{font-size: 30px;}}
        .inner{font-size: 30px;padding-left: 50px;}
        .head-contacts{font-size: 25px;}
        .contacts-p,.mail{font-size: 15px;}
    }
    @media(max-width:700px){
   
        .home-top{flex-direction: column;}
        .nav-left{display:none}
        .line{display: none;}
        #navbar{justify-content: flex-end;background: none;}
        .lang{color:grey}
        :root{
          --margins:10px;
        
        }
        .about-me{font-size:25px;}
        .skills-head{font-size: 25px;}
        .desk{margin-top: 15px;}
        #about{padding:100px 50px 0px 50px;}
        #skills{ padding:9px 10px 50px 10px;}
        .skills-img{height: 40px;}
        .both{font-size: 10px;}
        .home-top{h1{font-size: 25px;}}
        .inner{font-size: 30px;padding-left: 50px;}
    }
    @media(max-width:500px){
        .skills-img{height: 25px;}
        .inner{font-size: 25px;padding-left: 10px;}
    }